import Vue from 'vue'
import Vuex from 'vuex'
import api from '../services/api'

Vue.use(Vuex)

export default new Vuex.Store({
	strict: true, // нельзя изменять state без мутаций
	state: {
		user: null,
		token: null,
		isSignin: false,
		isSubjectsSidebar: false, // активность sidebar со списком предметов
		isAreasSidebar: false, // активность sidebar со списком Мо и Оо
		publishers: [], // Издатели для умк. Так как они не будут меняться часто, можно хранить их в store
		subjects: [],
		areas: [],
		levels: [],
		emcs: [],
		emcsOnSchool: [],
	},
	mutations: {
		setToken(state, token) {
			state.token = token
			window.localStorage.setItem('token', JSON.stringify(token))
			if (token) {
				state.isSignin = true
			} else {
				state.isSignin = false
			}
		},
		setUser(state, user) {
			state.user = user
		},
		setSubjectsSidebar(state, activity) {
			state.isSubjectsSidebar = activity
		},
		setAreasSidebar(state, activity) {
			state.isAreasSidebar = activity
		},
		setPublishers(state, publishers) {
			state.publishers = publishers
		},
		setSubjects(state, subjects) {
			state.subjects = subjects
		},
		setAreas(state, areas) {
			state.areas = areas
		},
		setLevels(state, levels) {
			state.levels = levels
		},
		setEMCs(state, emcs) {
			state.emcs = emcs
		},
		updateEMC(state, emc) {
			// Выполняется при измнениях в emc
			const existsAtIndex = state.emcs.indexOf(emc)
			if (existsAtIndex === -1) {
				// добавляем на случай, если умк открепили от организации текущего пользователя
				const response = api().post(`/${state.user.UserRole.name}/emcs/create`, emc)
				state.emcs.push(response.data.emc)
			} else {
				// заменяем, если изменения
				const response = api().put(`/${state.user.UserRole.name}/emcs/${emc.id}`, emc)
				state.emcs.splice(existsAtIndex, 0, response.data.emc)
			}
			state.emcs = [...state.emcs]
		},
		removeFromEMCs(state, emc) {
			// удаляем из списка на прикрепления для данного пользователя
			const existsAtIndex = state.emcs.indexOf(emc)
			if (existsAtIndex !== -1) {
				state.emcs.splice(existsAtIndex, 1, emc)
			}
			state.emcs = [...state.emcs]
		},
		deleteEMC(state, emc) {
			// удаляем насовсем
			const existsAtIndex = state.emcs.indexOf(emc)

			if (
				existsAtIndex !== -1 &&
				(state.user.UserRole === 1 ||
					(state.user.UserRole === (2 || 3) && emc.isCustom && emc.createdBy === state.user.id))
			) {
				const response = api().delete(`/${state.user.UserRole.name}/emcs/${emc.id}/delete`)
				state.emcs.splice(existsAtIndex, 1, response.data.emc)
				state.emcs = [...state.emcs]
			}
		},
	},
	actions: {
		setToken({ commit }, token) {
			commit('setToken', token)
		},
		setUser({ commit }, user) {
			commit('setUser', user)
		},
		setSubjectsSidebar({ commit }, activity) {
			commit('setSubjectsSidebar', activity)
		},
		setAreasSidebar({ commit }, activity) {
			commit('setAreasSidebar', activity)
		},
		setPublishers({ commit }, publishers) {
			commit('setPublishers', publishers)
		},
		setSubjects({ commit }, subjects) {
			commit('setSubjects', subjects)
		},
		setAreas({ commit }, areas) {
			commit('setAreas', areas)
		},
		setLevels({ commit }, levels) {
			commit('setLevels', levels)
		},
		setEMCs({ commit }, emcs) {
			commit('setEMCs', emcs)
		},
		updateEMC({ commit }, emc) {
			commit('updateEMC', emc)
		},
		removeFromEMCs({ commit }, emc) {
			commit('removeFromEMCs', emc)
		},
		deleteEMC({ commit }, emc) {
			commit('deleteEMC', emc)
		},
	},
})
